import React, { useReducer, useRef, useState } from 'react';
import { Card, Overlay, OverlayTrigger, Popover } from 'react-bootstrap';
import clsx from 'clsx';
import { createUseStyles as makeStyles } from 'react-jss';
import ProgressBar from 'react-bootstrap/ProgressBar';
import Icon from '@mdi/react';

import Image from 'react-image';
import theme from '../config/theme.config';
import * as icon from 'styles/icon/icon';

const useStyles = makeStyles({
  cardWrapper: {
    //width: '160px',
    display: 'inline-block',
    '& .card': {
      height: '200px',
      '& .card-title': {
        fontSize: '1rem',
        lineHeight: '1.25rem',
        position: 'relative',
        height: '2rem',
        '& $staffName': { position: 'absolute', bottom: 0, textAlign: 'center' }
      },
      '& .card-header': {
        marginBottom: '0',
        paddingLeft: '0.5rem',
        paddingRight: '0.5rem',
        paddingBottom: '0',
        '& img': { width: '64px', height: '64px' }
      },
      '& .card-body': { paddingTop: '0.5rem' },
      '& .data-sources': {
        display: 'none'
      },
      '& *': { cursor: 'default' }
    }
  },
  [theme.breakpoints.sm]: {
    cardWrapper: {
      // width: '200px'
    }
  },
  cardActions: {
    '&.popover': {
      boxShadow: 'none !important'
    },
    '& .arrow': { display: 'none' },
    backgroundColor: 'white',
    display: 'block',
    fontSize: '2rem',
    marginBottom: '0.5rem',
    padding: '0.5rem',
    boxShadow: 'none !important',
    cursor: 'default',
    '& $cardAction': {
      cursor: 'pointer'
    }
  },
  cardAction: {
    '& svg *': { strokeWidth: 0, fill: 'red' }
  }
});

const StaffCard = props => {
  const classes = useStyles();
  const target = useRef(null);
  const overlayContainer = useRef(null);

  const [show, setShow] = useState(false);

  const showCardActions = () => setShow(!props.isScrolling); // if we're currently scrolling, never show the card actions
  const hideCardActions = () => setShow(false);

  const defaultAvatar =
    'https://i1.wp.com/a.slack-edge.com/df10d/img/avatars/ava_0025-48.png?ssl=1';
  const defaultAvatarDataUri =
    'data:image/png;base64,';

  const avatarSources = [props.photoUrl, defaultAvatarDataUri];
  return (
    <>
      <div
        ref={target}
        onMouseEnter={showCardActions}
        onMouseLeave={hideCardActions}
        onClick={() => {
          showCardActions();
          setTimeout(() => hideCardActions(), 5000);
        }}
        className={clsx(classes.cardWrapper, 'm-0 p-1 p-md-2 text-center')}
        id={['staff-card', props.index].join('-')}
        style={props.style}
        //key={['staff-card', index].join('-')}
      >
        <Card className='staff-detail' border={props.status.toLowerCase()}>
          <Card.Header className='text-center'>
            <Card.Title
              className={clsx(classes.staffNameContainer, 'text-center')}
            >
              <div className={clsx(classes.staffName, 'w-100')}>
                {props.name.displayName}
              </div>
            </Card.Title>
            <Image
              src={avatarSources}
              alt={props.name.fullName}
              className='rounded-circle mx-auto'
              crossorigin='anonymous'
            />
          </Card.Header>
          <Card.Body className='text-left'>
            <span className='small-label'>{props.status}</span>
            <ProgressBar
              now={
                props.status.toLowerCase() === 'active'
                  ? 50 //15 + Math.random() * 100
                  : 0
              }
              variant='success'
            />
          </Card.Body>
        </Card>
        <div ref={overlayContainer} />
      </div>
      {!props.isScrolling && (
        <Overlay
          //container={overlayContainer.current}
          target={target.current}
          placement='bottom'
          show={show}
          onHide={hideCardActions}
          popperConfig={{
            modifiers: {
              flip: { enabled: false },
              inner: { enabled: true },
              arrow: { enabled: false }
            }
          }}
        >
          {({
            placement,
            scheduleUpdate,
            arrowProps,
            outOfBoundaries,
            show: _show,
            ...props
          }) => {
            return (
              <Popover
                {...props}
                className={classes.cardActions}
                onMouseEnter={showCardActions}
                onMouseLeave={hideCardActions}
              >
                <Icon
                  className={classes.cardAction}
                  path={icon.Accept}
                  size={1.5}
                />{' '}
                <Icon
                  className={classes.cardAction}
                  path={icon.AccountDetails}
                  size={1.5}
                />{' '}
                <Icon
                  className={classes.cardAction}
                  path={icon.Archive}
                  size={1.5}
                />
              </Popover>
            );
          }}
        </Overlay>
      )}
    </>
  );
};

export default React.memo(StaffCard);
