const theme = {
  breakpoints: {
    xs: '@media (min-width: 0px)',
    sm: '@media (min-width: 576px)',
    md: '@media (min-width: 768px)',
    lg: '@media (min-width: 992px)',
    xl: '@media(min-width: 1200px)',
    xxl: '@media(min-width: 1366px)',
    xxxl: '@media(min-width: 1800px)'
  }
};

export default theme;
